export default "@import '../../themes/src/default.css';\n\n:root {\n    /* border */\n    --plate-border-left: 2px;\n    --plate-border: 0 0 0 1px;\n    --plate-border-radius: var(--border-radius-m);\n\n    /* width limit */\n\n    --plate-width-limit: 560px;\n\n    /* custom view */\n    --plate-background: transparent;\n    --plate-border-color: var(--color-static-status-grey);\n    --plate-box-shadow: var(--shadow-s);\n}\n\n.component {\n    position: relative;\n    display: inline-flex;\n    box-sizing: border-box;\n    color: var(--color-light-text-primary);\n    width: 100%;\n    min-height: 56px;\n    padding: var(--gap-s);\n    outline: none;\n\n    &:before {\n        content: '';\n        width: var(--plate-border-left);\n        position: absolute;\n        height: 100%;\n        top: 0;\n        left: 0;\n    }\n}\n\n.foldable {\n    cursor: pointer;\n}\n\n.component * {\n    box-sizing: border-box;\n}\n\n.focused {\n    @mixin focus-outline;\n}\n\n.inner {\n    position: relative;\n    width: 100%;\n    display: flex;\n    justify-content: space-between;\n}\n\n.rounded,\n.noBorder {\n    &:before {\n        display: none;\n    }\n}\n\n.shadow:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    width: 100%;\n    height: 100%;\n    display: block;\n    box-shadow: var(--plate-box-shadow);\n    pointer-events: none;\n}\n\n.rounded {\n    border-radius: var(--plate-border-radius);\n    &.shadow:after {\n        border-radius: var(--plate-border-radius);\n    }\n}\n\n.common {\n    background-color: var(--color-light-specialbg-secondary-transparent);\n    box-shadow: var(--plate-border) var(--color-static-status-grey);\n    &:before {\n        background-color: var(--color-static-status-grey);\n    }\n}\n\n.negative {\n    background-color: var(--color-light-bg-negative-muted);\n    box-shadow: var(--plate-border) var(--color-static-status-red);\n    &:before {\n        background-color: var(--color-static-status-red);\n    }\n}\n\n.positive {\n    background-color: var(--color-light-bg-positive-muted);\n    box-shadow: var(--plate-border) var(--color-static-status-green);\n    &:before {\n        background-color: var(--color-static-status-green);\n    }\n}\n\n.attention {\n    background-color: var(--color-light-bg-attention-muted);\n    box-shadow: var(--plate-border) var(--color-static-status-orange);\n    &:before {\n        background-color: var(--color-static-status-orange);\n    }\n}\n\n.custom {\n    background: var(--plate-background);\n    box-shadow: var(--plate-border) var(--plate-border-color);\n    &:before {\n        background-color: var(--plate-border-color);\n    }\n}\n\n.rect,\n.noBorder {\n    box-shadow: none;\n}\n\n.isHidden {\n    display: none;\n}\n\n.leftAddons,\n.rightAddons {\n    display: inline-flex;\n    padding: var(--gap-2xs);\n}\n\n.leftAddons {\n    flex-shrink: 0;\n    min-width: 24px;\n    margin-right: var(--gap-2xs);\n}\n\n.subAddons {\n    cursor: default;\n    align-self: flex-start;\n}\n\n.bold,\n.light {\n    word-break: break-word;\n}\n\n.bold {\n    @mixin headline-system_xsmall;\n    padding: 5px var(--gap-s) 5px var(--gap-2xs);\n}\n\n.light {\n    @mixin paragraph_primary_small;\n    padding: 6px var(--gap-s) 6px var(--gap-2xs);\n}\n\n.foldable .content {\n    max-height: 300vh;\n}\n\n.content {\n    @mixin paragraph_primary_small;\n\n    overflow: hidden;\n    transition: max-height 2s ease-in-out;\n    cursor: default;\n\n    &.isFolded.isFolded {\n        max-height: 0;\n        transition: max-height 1s cubic-bezier(0, 1, 0, 1);\n    }\n}\n\n.contentContainer {\n    flex: 1;\n    margin-right: auto;\n\n    &.withoutTitle {\n        padding-top: var(--gap-3xs);\n    }\n}\n\n.limitWidth {\n    max-width: var(--plate-width-limit);\n}\n\n.description {\n    @mixin paragraph_primary_small;\n    padding: var(--gap-2xs) var(--gap-s) var(--gap-2xs) var(--gap-2xs);\n}\n\n.footer {\n    padding: var(--gap-l) 0 var(--gap-2xs) var(--gap-2xs);\n}\n\n.closer,\n.folder {\n    display: block;\n    height: 24px;\n    width: 24px;\n    opacity: 1;\n    transition: opacity 0.2s ease;\n    cursor: pointer;\n    margin-left: var(--gap-s);\n\n    &:hover {\n        opacity: 0.7;\n    }\n\n    &:active {\n        opacity: 0.5;\n    }\n}\n\n.folder {\n    transition: transform 0.15s ease-in-out, opacity 0.2s ease;\n    transform: var(--arrow-transform);\n\n    &.isFolded {\n        transform: rotate(0deg);\n    }\n}\n\n.button {\n    &:not(:last-child) {\n        margin-right: var(--gap-xs);\n    }\n}\n\n.rowLimit1 {\n    @mixin row_limit 1;\n    word-break: break-all;\n}\n\n.rowLimit2 {\n    @mixin row_limit 2;\n}\n\n.rowLimit3 {\n    @mixin row_limit 3;\n}\n";